.advantages {
  padding: 90px 0;
  text-align: center;

  @media (max-width: $mobile) {
    padding: 48px 0;
  }

  &__wrapper {
    width: 85%;
  }

  &__items {
    padding-right: 16px;
    padding-top: 50px;
    padding-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (max-width: $mobile) {
      padding-top: 35px;
      padding-bottom: 0;
      padding-right: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: calc(100% / 3 - 45px);
    margin-bottom: 35px;

    &:nth-child(2n) {
      & .advantages__text {
        padding-top: 16px;
      }
    }

    @media (max-width: $mobile) {
      width: 100%;

      &:nth-child(2n) {
        & .advantages__text {
          padding-top: 0;
        }
      }
    }
  }

  &__icon {
    margin-bottom: 30px;
    width: 85px;
    height: 85px;

    @media (max-width: $mobile) {
      margin-bottom: 2px;
      width: 55px;
      height: 55px;
    }
  }
}
