.social {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-bottom: 52px;

  @media (max-width: $mobile) {
    padding-bottom: 45px;
  }

  &__subtitle {
    font-family: $RobotoMedium;
    font-size: 20px;
    line-height: 23px;
    text-align: center;

    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 18px;

      & span {
        display: block;
      }
    }
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0;

    @media (max-width: $mobile) {
      padding: 12px 0 0;
    }
  }

  &__link {
    display: block;
    margin-right: 100px;
    color: $default-color;
    text-decoration: none;

    @media (max-width: $mobile) {
      margin-right: 78px;
    }

    &:last-child {
      margin-right: 0;
    }

    &:focus,
    &:hover {
      opacity: $default-opacity;
    }
  }

  &__icon {
    width: 60px;
    height: 60px;
    fill: $default-color;

    @media (max-width: $mobile) {
      height: 35px;
      width: 35px;
    }
  }
}
