.skills {
  padding: 90px 0 92px;

  @media (max-width: $mobile) {
    padding: 35px 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    padding-top: 52px;

    @media (max-width: $mobile) {
      padding-top: 35px;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 78px;

    width: 100%;

    &:first-child {
      & .skills__text {
        max-width: 435px;
      }
    }

    &:nth-child(2n) {
      justify-content: flex-end;

      & .skills__text {
        max-width: 465px;
        margin-left: auto;
      }

      @media (max-width: $mobile) {
        justify-content: flex-start;
      }

      & .skills__icon {
        order: 2;
        margin-right: 0;
        margin-left: 26px;

        @media (max-width: $mobile) {
          order: 0;
          margin-left: 0;
        }
      }

      & .skills__info {
        padding-top: 22px;
        text-align: right;

        @media (max-width: $mobile) {
          text-align: center;
        }
      }
    }

    &:last-child {
      margin-bottom: 0;

      & .skills__info {
        padding-top: 10px;
      }

      & .skills__text {
        max-width: 390px;
      }
    }

    @media (max-width: $mobile) {
      display: none;
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 30px;
      }

      &:first-child {
        display: flex;
      }
    }
  }

  &__icon {
    width: 175px;
    margin-right: 26px;

    @media (max-width: $mobile) {
      margin-right: auto;
      margin-left: auto;
      margin-bottom: 10px;
    }
  }

  &__info {
    width: calc(100% - 175px - 26px);
    max-width: 570px;

    @media (max-width: $mobile) {
      width: 100%;
      text-align: center;
    }
  }

  &__subtitle {
    @media (max-width: $mobile) {
      padding: 0;
    }
  }

  &__text {
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 17px;

      & span {
        display: block;
      }
    }
  }

  &__pagination {
    display: none;

    @media (max-width: $mobile) {
      display: flex;
    }
  }
}
