.section {
  &--bg {
    background-color: $bg-color;
  }

  &__title {
    margin: 0;
    font-family: $mainFontExtraBold;
    font-size: 30px;
    line-height: 37px;

    @media (max-width: $mobile) {
      padding: 0 10px;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 10px;

    font-size: 30px;
    line-height: 37px;
    font-weight: 600;

    @media (max-width: $mobile) {
      margin-bottom: 5px;
      padding: 0 35px;

      font-size: 16px;
      line-height: 20px;
    }

    &--bold {
      font-family: $mainFontExtraBold;
    }
  }

  &__text {
    margin: 0;
    font-size: 20px;
    line-height: 24px;

    @media (max-width: $mobile) {
      padding: 0 8px;
      font-size: 16px;
      line-height: 20px;
    }
  }
}
