.page-footer {
  padding: 60px 0;
  color: $default-color;
  background-color: $secondary-color;

  @media (max-width: $mobile) {
    padding: 30px 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__copy {
    font-family: $Roboto;
    font-size: 20px;
    line-height: 23px;

    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 16px;
    }
  }
}
