.feedback {
  padding: 60px 0;
  background-color: #e7e0f3;

  @media (max-width: $mobile) {
    padding: 30px 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__icon {
    margin-top: 35px;
    margin-bottom: 50px;

    @media (max-width: $mobile) {
      margin-top: 20px;
      margin-bottom: 30px;
    }
  }

  &__text {
    max-width: 375px;
    text-align: center;
  }

  &__form {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    max-width: 300px;
    width: 100%;
    padding-top: 35px;

    @media (max-width: $mobile) {
      padding-top: 20px;
    }
  }

  &__label {
    width: 100%;
    margin-bottom: 20px;

    @media (max-width: $mobile) {
      margin-bottom: 10px;
    }
  }

  &__field {
    width: 100%;
    padding: 25px 40px;
    background: #fbfbfb;
    border: 1px solid #e9e9e9;
    border-radius: $default-borderRadius;
    outline: none;

    font-family: inherit;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;

    @media (max-width: $mobile) {
      padding: 18px 25px;
      font-size: 10px;
      line-height: 12px;
    }
  }

  &__button {
    margin-top: 16px;
    padding: 24px 35px;
    color: $default-color;
    background-color: $btn-bg-color;

    @media (max-width: $mobile) {
      margin-top: 20px;
      padding: 15px 35px;
    }
  }
}
