.menu {
  display: flex;
  flex-direction: column;
  align-self: flex-end;

  @media (max-width: $mobile) {
    flex-direction: row;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-left: auto;
    margin-right: 0;
  }

  &__button {
    display: none;
    padding: 0;
    background-color: transparent;
    border: none;
    border-radius: 0;
    outline: none;
    cursor: pointer;

    &:focus,
    &:hover {
      outline: none;
    }

    @media (max-width: $mobile) {
      order: 2;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      align-self: flex-end;
      margin-left: 17px;
    }
  }

  &__link {
    padding: 13px 11px;
    margin-right: 27px;
    font-family: $secondaryFontExtraBold;
    font-size: inherit;
    line-height: inherit;
    color: $primary-color;
    text-decoration: none;
    border-radius: 50px;
    transition: background-color $default-transition;

    &:last-child {
      margin-right: 0;
    }

    &:focus,
    &:hover {
      color: $default-color;
      background-color: $secondary-color;
      border-radius: 50px;
    }

    @media (max-width: $mobile) {
      display: none;
      font-size: 16px;
      line-height: 20px;
    }

    &--active {
      display: flex;
      padding: 13px 33px;
      font-family: $mainFontBold;
      font-weight: 800;
      color: $default-color;
      background-color: $secondary-color;
      border-radius: 50px;
      transition: opacity $default-transition;

      &:focus,
      &:hover {
        opacity: $default-opacity;
      }

      @media (max-width: $mobile) {
        padding: 3px 13px;
        font-size: 8px;
        line-height: 16px;
      }
    }
  }
}
