.pagination {
  display: none;

  @media (max-width: $mobile) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  &__item {
    padding: 0;
    margin-right: 16px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: $default-color;
    border: 1px solid #e1e6e9;
    outline: none;

    &:last-child {
      margin-right: 0;
    }

    &:focus,
    &:hover {
      background-color: $warning-color;
      border-color: $warning-color;
      outline: none;
    }

    &--active {
      background-color: $warning-color;
      border-color: $warning-color;
    }
  }
}
