.page-header {
  position: relative;
  padding: 30px 0;

  background-color: $default-color;

  border-top: 3px solid $secondary-color;
  border-bottom: 3px solid $secondary-color;

  @media (max-width: $mobile) {
    padding: 14px 0;
    border: none;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }
}
