.promo {
  text-align: right;

  padding: 150px 0;
  background-image: url("../images/promo/promo-bg.jpg");

  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #e5c411;

  @media (max-width: $mobile) {
    background-position: -48px 0;
    padding: 25px 0 118px;
    background-image: url("../images/promo/promo-bg-mob.jpg");
  }

  @media (min-resolution: 120dpi), (min-resolution: 1.25dppx) {
    background-image: url("../images/promo/promo-bg-mob@2x.jpg");
  }

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../images/promo/promo-bg@2x.jpg");
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }

  &__title {
    align-self: flex-end;
    margin: 0;
    margin-bottom: 8px;
    max-width: 566px;
    font-family: $mainFontExtraBold;
    font-size: 40px;
    line-height: 49px;
    color: $secondary-color;

    @media (max-width: $mobile) {
      margin-bottom: 0;
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__text {
    align-self: flex-end;
    margin: 0;
    max-width: 340px;
    font-family: $mainFontBold;
    font-size: 30px;
    line-height: 37px;
    color: $default-color;

    @media (max-width: $mobile) {
      max-width: 240px;
      font-size: 16px;
      line-height: 20px;
    }
  }
}
